import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import type { ITooltipCell } from '../../../../types';

export interface TextImageCellData {
    text: string;
    image: string;
    tooltip?: string;
}

export interface ITextImageCellParams
    extends ICellRendererParams,
        ITooltipCell {
    text?: (data: any) => string;
    imageSource: (data: any) => string;
    imageHeight?: number;
    imageWidth?: number;
    imageClass?: string;
    imagePosition?: 'before' | 'after';
}

/**
 * @category Table Cell
 * @description A cell component for displaying a text with an image.
 */
@Component({
    tag: 'atui-text-image-cell',
    shadow: false,
})
export class AtuiTextImageCell implements ICellRendererComp {
    @Element() el: HTMLElement;

    private params: ITextImageCellParams;

    @State() text: string = '';

    private imageHeight: number = 24;
    private imageWidth?: number;
    private imageClass?: string;
    private imagePosition: 'before' | 'after' = 'before';

    init(params: ICellRendererParams): void {
        this.params = params as ITextImageCellParams;

        this.imageHeight = this.params.imageHeight ?? this.imageHeight;
        this.imageWidth = this.params.imageWidth;
        this.imageClass = this.params.imageClass;
        this.imagePosition = this.params.imagePosition ?? 'before';

        this.updateTextAndImage();
    }

    refresh(params: ICellRendererParams): boolean {
        this.params = params as ITextImageCellParams;
        this.updateTextAndImage();
        return true;
    }

    private updateTextAndImage() {
        this.text = this.params.text
            ? this.params.text(this.params.data)
            : (this.params.value ?? '');
    }

    getGui(): HTMLElement {
        return this.el;
    }

    private get imageSource() {
        return this.params.imageSource(this.params.data);
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                <atui-tooltip
                    position="right"
                    disabled={!this.params?.generateTooltip}
                >
                    <div
                        class="flex items-center gap-8"
                        slot="tooltip-trigger"
                    >
                        {this.imagePosition === 'before' &&
                            this.imageSource && (
                                <img
                                    src={this.imageSource}
                                    style={{
                                        height: `${this.imageHeight}px`,
                                        width: this.imageWidth
                                            ? `${this.imageWidth}px`
                                            : undefined,
                                    }}
                                    class={this.imageClass && this.imageClass}
                                    alt=""
                                />
                            )}

                        {this.text && <span class="truncate">{this.text}</span>}

                        {this.imagePosition === 'after' && this.imageSource && (
                            <img
                                src={this.imageSource}
                                style={{
                                    height: `${this.imageHeight}px`,
                                    width: this.imageWidth
                                        ? `${this.imageWidth}px`
                                        : undefined,
                                }}
                                class={this.imageClass && this.imageClass}
                                alt=""
                            />
                        )}
                    </div>

                    {this.params?.generateTooltip && (
                        <span>{this.params.generateTooltip(this.params)}</span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
